* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.content {
  height: 120vh;
}

.register-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

/*transaction page*/
.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 4px grey;
}
/*transaction page*/

/* antd icons */

.icons {
  border: 1px solid black;
  border-radius: 5px;
  padding: 10px 15px;
}
.anticon svg {
  font-size: 15px;
  cursor: pointer;
}

.active-icon {
  color: black;
}

.inactive-icon {
  color: green;
}
